.small-tree-indent { margin: 0 6px; display: inline-block; }
.icon-hover { visibility: hidden; opacity: 0; transition: all .2s ease-in-out; }
.row_data:hover .icon-hover{ visibility: visible; opacity: 1; }
.capitalize { text-transform: capitalize; }
.badge { font-size: 12px; font-weight: normal; }
.icon_parent { background: transparent; width: 20px; height: 20px; display: inline-block; vertical-align: middle; text-align: center; margin: 0 2px 0 0;
    i { font-size: 16px; line-height: 10px; }
    &:last-child { margin: 0; }
}
.expanded_icon { box-sizing: border-box; position: relative; transform: scale(var(--ggs,1)); width: 18px; height: 18px; border: 2px solid transparent; border-radius: 100px; }
.expanded_icon:after { content: ""; box-sizing: border-box; position: absolute; width: 8px; height: 8px; border-bottom: 2px solid; border-right: 2px solid; transform: rotate(-45deg); right: 4px; top: 6px; }
.expanded:after { content: ""; box-sizing: border-box; position: absolute; width: 8px; height: 8px; border-bottom: 2px solid; border-right: 2px solid; transform: rotate(45deg); top: 4px; }
.tree-checkbox { position: relative; top: -1px; min-width:24px; height:24px; margin:0 10px 0 0; --checkmark-color:transparent; --background:transparent; background: url("/src/assets/images/ico_chkbox.svg") no-repeat center; background-size: 24px auto; --border-width:0; }
.tree-checkbox:after, .tree-arrow:after { position: absolute; display: block; content: ""; }
.tree-checkbox.checked, .tree-checkbox.indeterminate { --background-checked:transparent; background: #1E73B3 url("/src/assets/images/ico_chkbox_check.svg") center no-repeat; border-radius: 4px; }
