.tree-checkbox.indeterminate:after { background-color: #FFFFFF; top: 50%; left: 20%; right: 20%; height: 2px; }
.tree-anchor { flex-grow: 2; outline: none; display: flex; text-decoration: none; color: #343434; vertical-align: top; margin-left: 3px; line-height: 24px; padding: 3px 6px; -webkit-user-select: none; -moz-user-select: none; -ms-user-select: none; user-select: none; }
.tree-node.selected > .tree-content > .tree-anchor { outline: none; }
.tree-node.disabled > .tree-content > .tree-anchor { color: #989191; background: #FFFFFF; opacity: .6; cursor: default; outline: none; }
.tree-input { display: block; width: 100%; height: 24px; line-height: 24px; outline: none; border: 1px solid #3498db; padding: 0 4px; }

/* Create by Denish */
.tree_list { 
    > .tree_item { border-bottom: 1px solid rgba($color: #fff, $alpha: .5);}
    .item { display: flex; padding: 8px 0; align-items: center;
        .tree_name { flex: 1 auto; color: #333333;
            .tree_ico { margin-right: 12px;}
        }
        .selected { font-weight: bold;}
    }
    .tree_ico_left { margin-right: 12px;}
    .tree_ico_right { margin-left: 12px;}
}
.sub_tree_list { 
    .tree_item { border-bottom: 1px solid rgba($color: #fff, $alpha: .5); 
        &:last-child { border-bottom: none; margin-bottom: 0;}
    }
}
.item{
    &:hover {  background-color: #EEF4FF; text-decoration: underline;}
}
.open-tree-folder{ background: url("/src/assets/images/ico_folder.svg") left center; background-repeat: no-repeat; padding-left: 10px; width: 0px; overflow: hidden; padding-left: 20px; }

.close-tree-folder{ background-repeat: no-repeat; background-image: url("/src/assets/images/ico_folder_open.svg"); width: 0px; overflow: hidden; padding-left: 20px; line-height: 20px; }
.active{ color: rgb(0, 0, 0); font-weight: bold; background-color: #EEF4FF; padding: 10px 10px 10px 40px; border-radius: 8px }
.sub-tree-icon{ background-repeat: no-repeat; width: 0px; overflow: hidden; padding-left: 20px; background-image: url("/src/assets/images/ico_screen.svg"); line-height: 20px; }


.detail { color: #999999;}