tr, th {
    padding: 20px !important
}
tr, td {
    padding: 16px !important
}
.invalidInput {
    border: 1px solid red !important;
}
.invalidRadio {
    border: 1px solid red !important;
    border-radius: 50%;
}
.menu_mgmt {
    h4 { color: #000000; font-size: 16px; font-weight: 700; margin-bottom: 20px;}
    .menu_level { padding: 20px 0 0;
        > li { margin-bottom: 40px;}
        > li > p { margin-bottom: 10px; color: #000000; font-size: 16px; font-weight: 700; line-height: 24px; cursor: pointer;
            &:hover { text-decoration: underline;}
        }
    }
    .sub_lvl { margin-left: 30px;
        > li > p { margin-bottom: 10px; color: #000000; font-size: 16px; font-weight: 500; line-height: 24px;  cursor: pointer;
            &:hover { text-decoration: underline;}
        }
        .link { color: #1D74F6; font-size: 16px; font-weight: 500; line-height: 24px; text-decoration-line: underline;}
    }
    .folder { padding-left: 30px; background: url("../../../assets/images/ico_folder.svg") left center no-repeat;
        &.open { background-image: url("../../../assets/images/ico_folder_open.svg");}
    }
    .screen { padding-left: 30px; background: url("../../../assets/images/ico_screen.svg") left center no-repeat;}
    .selected { padding: 10px 10px 10px 40px; border-radius: 8px; background-color: #FFF9E0; background-position: left 10px center;}
 }

 .card_box { 
    > .col { display:  flex; flex-direction: column;
        .menu_level { flex: 1; overflow: auto;}
    }
    .menu_detail_wrap { display:  flex; flex-direction: column;}
    .menu_detail { display:  flex; flex-direction: column; flex: 1; overflow: auto; border: 1px solid #ddd; border-radius: 12px; -webkit-border-radius: 12px; -moz-border-radius: 12px; -ms-border-radius: 12px; -o-border-radius: 12px; padding: 20px 20px 40px;}
    .cont { flex: 1;}
 }

 .btn_option { display: flex; justify-content: space-between; margin-top: 40px;    
    &.right { justify-content: flex-end;}
    > div { 
        ion-button { margin-left: 20px;}
    }
}

.form01 {
    ion-item { margin-bottom: 10px;
        ion-label { color: #000000; font-size: 16px; font-weight: 700;}
    }
    ion-item + ion-label { font-weight: 500;}
}
.tableWrap { height: auto;}